@import url(https://use.typekit.net/tcr2vad.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  width: 100%;
  height: auto;
  font-family: noto-sans, sans-serif;
}

.App::before {
  content: " ";
  position: fixed;
  top: 0px;
  left: 0px;
  height: 100vh;
  width: 100vw;
  background-size: cover;
}
ul {
  padding: 0px;
}
li {
  list-style: none;
}
.hide-me {
  position: fixed;
  visibility: hidden;
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-thumb {
  background: #888; 
}

html {
  height: 100%;
}

#root {
  height: 100%;
}
#cover-me{position:fixed;top:0px;z-index:1;height:100%}body{position:relative;width:100%;height:100%}.enter{-webkit-animation-name:enter;animation-name:enter;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-iteration-count:1;animation-iteration-count:1}@-webkit-keyframes enter{from{transform:0}to{opacity:1}}@keyframes enter{from{transform:0}to{opacity:1}}@-webkit-keyframes exit{from{opacity:1}to{opacity:0}}@keyframes exit{from{opacity:1}to{opacity:0}}.container-header{height:auto;min-height:100vh;box-sizing:border-box;margin:0;z-index:2;flex-wrap:wrap;align-items:center;padding:0px;grid-template-rows:100px 1fr 150px;display:grid;position:relative}.container-header header h1{margin:0px;word-wrap:break-word;font-size:50px}.container-header header h3{margin:0;margin-top:-10px;font-size:1rem;line-height:2.5rem;letter-spacing:0.5rem;font-family:noto-sans, sans-serif;text-transform:uppercase}

.skills{z-index:3;position:relative}.skills ul{margin:0px;padding:0 0 0 10px;display:flex;flex-wrap:wrap;border-left-style:solid;border-left-width:1px}@media screen and (max-width: 600px){.skills ul{padding-top:10px;border-top-style:solid;border-left-width:0px;border-top-width:1px}}.skills ul li{display:inline-block;margin-right:5px;padding:5px;margin:5px;position:relative;font-size:1rem;cursor:pointer;transition:.2s}.skills ul li::before{content:" ";position:absolute;width:100%;height:100%;top:0px;left:0px;opacity:0.2}.skills h1{padding:0px;margin:0px;font-size:1rem;text-align:left;text-transform:uppercase}.skills h2{text-align:left;font-size:1.5rem;font-family:brevia, sans-serif;margin:0px;padding:0px}.skills h3{font-size:1rem;font-family:protipo, sans-serif;margin:0px;text-align:left;padding-bottom:20px;text-transform:uppercase;letter-spacing:2px}.skills h4{display:inline-block;padding-right:10px;text-align:right;height:100%;width:100%;box-sizing:border-box;padding:0 10px;font-size:2.5rem;line-height:2.5rem;margin:0px}.skills .skills-container{margin:auto;display:grid;grid-template-rows:80px auto auto;grid-row-gap:10px;position:relative;padding:20px;box-sizing:border-box;justify-content:center;align-items:center}.skills .skills-container div{max-width:900px;display:grid;grid-template-columns:repeat(auto-fill, minmax(300px, 1fr));align-items:start;justify-items:start;justify-content:start;padding:30px;box-sizing:border-box;align-content:start;position:relative;z-index:4;border-radius:5px;width:100%;overflow:hidden;grid-gap:10px}

.top-nav{z-index:10;position:-webkit-sticky;position:sticky;top:0px;display:flex;justify-content:flex-end;align-items:center;flex-wrap:wrap;width:100%;height:100%;text-align:right;min-height:50px;line-height:50px;box-sizing:border-box;margin:0px}.top-nav a{margin:0px 10px;text-decoration:none;text-transform:uppercase;position:relative;font-weight:600;opacity:1;height:20px}.top-nav a:hover{transition:0.2s}

.web-container{height:100%;text-align:left;box-sizing:border-box;display:grid;grid-template-columns:1fr;justify-items:center;align-items:start}.web-container a{color:#008aff;text-decoration:none;font-weight:600}.web-container h1,.web-container h2{padding:0px;margin:0px}@media screen and (max-width: 700px){.web-container .control-header{display:grid;grid-template-columns:70px 1fr;position:relative}.web-container .control-header .prev{grid-column:1;grid-row:1}.web-container .control-header .header-text{grid-row:2;grid-column:1/-1}.web-container .control-header .next{grid-column:2;grid-row:1}}.web-container h1{font-size:1rem;text-transform:uppercase}.web-container h2{font-family:brevia, sans-serif;text-transform:capitalize}.web-container h3{text-transform:uppercase}.web-container p{padding:0px;margin:0px}.web-container .inactive-button{opacity:0}.web-container .next-prev{position:fixed;top:50%;width:100%}@media screen and (max-width: 500px){.web-container .next-prev{width:100%;top:90%;position:fixed;height:200px}}.web-container .act-button{opacity:1;background:white;border-radius:50%;width:35px;height:35px;display:inline-flex;align-content:center;justify-content:center;align-items:center;font-size:1.5rem;cursor:pointer;color:#5d5d5d;margin:10px}.web-container .act-button:hover{border-radius:25px;transition:0.2s;background:#008aff;color:white}.web-container .active-button{opacity:1;background:white;border-radius:50%;width:50px;height:50px;display:flex;align-content:center;justify-content:center;align-items:center;font-size:1.5rem;cursor:pointer;top:30px;position:relative}.web-container .active-button:hover{border-radius:25px;transition:0.2s;background:#008aff;color:white}.web-container .next-b{position:absolute;right:10px}.web-container .next-b:hover{padding-left:20px}.web-container .prev-b{position:absolute;left:10px}.web-container .prev-b:hover{padding-right:20px}.web-container .proj-image{display:flex;flex-wrap:wrap;grid-gap:10px}.web-container .proj-image span{width:auto;display:flex;max-width:200px;max-height:200px;display:flex;align-items:center;align-content:center;justify-items:center;justify-content:center;text-decoration:none;padding:10px;box-sizing:border-box;text-align:center;position:relative;cursor:pointer;border-radius:5px}.web-container .proj-image span:hover{transition:.5s;color:black;background:white}.web-container .proj-image span img{width:100%;object-fit:contain;height:100%;border-radius:5px}.web-container .proj-image span .alt-show{position:absolute;top:0px;left:0px;height:100%;background:rgba(255,255,255,0.8);display:grid;font-weight:600;align-items:center;padding:5px;box-sizing:border-box}.web-container .tags{display:flex;flex-wrap:wrap}.web-container .tags span{cursor:pointer;padding:10px;margin:5px}.web-container .tags .active-tag{background:#DAA520 !important}.web-container .project-container{width:100%;padding:30px 50px;box-sizing:border-box;position:relative;max-width:900px;margin:10px auto;border-radius:10px;z-index:2;padding-top:30px}.web-container .project-container .preview-image{margin:15px}.web-container .project-container .preview-image img{width:100%;max-width:400px;float:right;border-radius:5px}.web-container .project-container .preview-image iframe{min-height:400px;width:100%;border:0px;outline:0px}.web-container .project-container .desc-container{margin:10px 0}.web-container .project-container .links-container a{margin:5px;text-decoration:none;color:#008aff;font-weight:600}.web-container .project-container .links-container a:hover{color:#008aff;border-bottom:3px solid #008aff}.web-container .project-container .top-right{position:absolute;top:5px;right:20px;text-align:right}.web-container .project-container .top-right p{margin-bottom:10px}.web-container .project-container .top-right .gold-text{font-weight:800;color:#daa520;padding-left:10px}.web-container .project-container .top-right button{cursor:pointer;border-radius:5px;box-sizing:border-box;padding:5px 10px;outline:0px;font-weight:600;box-shadow:1px 1px 5px rgba(0,0,0,0.1)}.web-container .project-container .top-right button:hover{box-shadow:none}

.art-gallery-container{display:grid;grid-template-columns:200px 1fr;margin:50px auto;max-width:900px;width:100%;justify-items:center;padding:30px;border-radius:10px;grid-gap:20px;box-sizing:border-box;min-height:600px}@media screen and (max-width: 600px){.art-gallery-container{grid-template-columns:1fr}}.art-gallery-container h1,.art-gallery-container h2{padding:0px;margin:0px}.art-gallery-container h1{font-size:1rem;text-transform:uppercase}.art-gallery-container h2{font-family:brevia, sans-serif;text-transform:capitalize}.art-gallery-container .sidebar{text-align:left;position:relative}.art-gallery-container nav{position:absolute;bottom:0px}@media screen and (max-width: 600px){.art-gallery-container nav{position:relative;text-align:center}}.art-gallery-container nav ul{line-height:2.5rem}@media screen and (max-width: 600px){.art-gallery-container nav ul{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:10px}}.art-gallery-container nav ul li{cursor:pointer}.art-gallery-container nav ul li:hover{color:#DAA520;background:none}.art-gallery-container nav ul .active-tab{padding-left:10px;color:#DAA520;font-weight:800;border-left:10px solid #DAA520}@media screen and (max-width: 600px){.art-gallery-container nav ul .active-tab{border-left:0px;border-bottom:10px solid #DAA520}}.art-gallery-container .desc{min-height:100px}.art-gallery-container .button-container{display:flex;align-items:center;justify-items:center;align-content:center;justify-content:center;-webkit-user-select:none;-ms-user-select:none;user-select:none}.art-gallery-container .button-container .circle-button{margin:10px;display:flex;padding:10px;border-radius:50%;width:20px;height:20px;align-items:center;justify-items:center;align-content:center;justify-content:center;cursor:pointer}.art-gallery-container .button-container .circle-button:hover{transition:0.2s;background:#008aff;color:white}.art-gallery-container .content-container{width:100%;overflow-x:hidden;position:relative}.art-gallery-container .main-image{position:-webkit-sticky;position:sticky;left:0px;margin-bottom:30px}.art-gallery-container .main-image img,.art-gallery-container .main-image iframe{border-radius:5px;object-fit:cover;max-width:800px;width:100%}.art-gallery-container .main-image iframe{width:100%;height:400px;border:0px;outline:0px}.art-gallery-container .gallery-image-container{display:flex;overflow-x:auto;width:100%}.art-gallery-container .gallery-image-container a:focus{border-top:3px solid white;outline:0px}.art-gallery-container .gallery-image-container img{cursor:pointer;height:100px;width:auto;object-fit:cover;margin:10px;border-radius:5px}.art-gallery-container .gallery-image-container p{display:flex;align-items:center;justify-items:center;align-content:center;justify-content:center;padding:10px;border-radius:5px;cursor:pointer;margin:15px 10px;box-sizing:border-box;min-width:100px;border:1px solid rgba(255,255,255,0.3)}.art-gallery-container .gallery-image-container p:hover{transition:.2s;background:rgba(255,255,255,0.3)}

.mode{font-size:1.5rem;position:relative;display:grid;grid-template-columns:repeat(3, 70px);grid-gap:5px;margin:auto;max-width:300px;padding:10px;box-sizing:border-box;border-radius:55px;overflow:auto;font-weight:400;font-family:great-vibes, sans-serif;align-items:center;justify-content:center}.mode span{cursor:pointer}.switch{position:relative;justify-self:center;width:60px;height:34px}.switch input{opacity:0;width:0;height:0}.slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;transition:0.4s;box-shadow:0px 0px 16px rgba(0,0,0,0.1);overflow:auto}.slider:before{position:absolute;content:"";height:26px;width:26px;left:4px;bottom:4px;background-color:#363636;transition:0.4s;border:1px solid gray}input:checked+.slider{box-shadow:0px 0px 16px rgba(122,97,62,0.2)}input:checked+.slider:before{background-color:white}input:checked+.slider:before{transform:translateX(26px)}.slider.round{border-radius:34px}.slider.round:before{border-radius:50%}

footer{width:100%;height:100%;box-sizing:border-box;padding:10px}

.contact-container{z-index:3;position:-webkit-sticky;position:sticky;padding:10px;top:30px;box-sizing:border-box}.contact a{color:white;font-weight:800;z-index:5;font-size:14px}.contact-list{display:flex;justify-content:center;margin-left:auto}.contact-list a{display:flex;word-break:break-word;color:white;text-decoration:none;height:40px;margin:10px;align-items:center}.contact-list img{height:100%;max-width:30px;object-fit:contain;transition:.2s}.contact-list a:hover img{transition:.2s;-webkit-filter:grayscale(100%) brightness(120%);filter:grayscale(100%) brightness(120%)}

